.app-wrapper {
  display: grid;

  margin: 0 auto;
  width: 1400px;

  grid-template-areas:
    "h h"
    "n c";

  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;
  gap: 10px;
}

.header {
  grid-area: h;
  background-color: #8507ec;
}

.header img {
  width: 20px;
}

.nav {
  grid-area: n;
  background-color: #6603b8;
}

.content {
  grid-area: c;
  background-color: #ebe3e3;
}

.content img {
  width: 100%;
  max-height: 300px;
}
